body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

*::-moz-selection {
  background: #1890ff;
}

*::selection {
  background: #1890ff;
}

.logo {
  width: 120px;
  height: 31px;
  background: rgba(255, 255, 255, 0.2);
  margin: 16px 28px 16px 0;
  float: left;
}

.logo img {
  position: relative;
  bottom: 20px;
}

.header {
  background: #fff;
}

.content_wrapper .content .header {
  text-align: center;
  font-size: 25px;
  border-bottom: 1px solid #eee;
  padding: 1em;
  margin-bottom: 2em;
}

.content_wrapper .content .header h4 {
  font-size: 17px;
  font-weight: 300;
  position: relative;
  bottom: 15px;
}

.content_wrapper .content .body .section {
  margin-bottom: 2em;
}

.content_wrapper .content .body .section .title {
  margin-bottom: 15px;
  display: block;
  font-weight: 700;
}

.content_wrapper .content .body .section p {
  padding-left: 2em;
}

.content_wrapper .content .body .section .citat {
  background: #f2f2f2;
  padding: 2px 6px;
  width: auto;
  border-radius: 2px;
  font-style: italic;
}

.content_wrapper .content .poezie {
  background: #f4f4f4;
  padding: 2em;
  border-radius: 8px;
}

.content_wrapper .content .extra {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  position: relative;
  bottom: 3em;
}

.content_wrapper .content .extra button {
  background: #1890ff;
  border: none;
  color: #fff;
  padding: 5px 15px;
  border-radius: 33px;
  margin-left: 15px;
}

.content_wrapper .content .extra button:focus {
  outline: none;
}

.content_wrapper .content .extra button span {
  margin-left: 15px;
}

@media print {
  body * {
    visibility: hidden;
  }
  .title * {
    page-break-before: always;
  }
  .content_wrapper *,
  .logo * {
    visibility: visible;
  }
  .content_wrapper .extra * {
    display: none;
  }
  .logo {
    width: 40px;
  }
  .content_wrapper,
  .logo {
    position: absolute;
    left: 0;
    top: 0;
  }
}

.romana_intro .content .header .left {
  padding: 4em;
}

.romana_intro .content .header .left h1 {
  font-size: 27px;
}

.romana_intro .content .header .left h4 {
  font-weight: 300;
  font-size: 18px;
}

.romana_intro .content .header .right .responsiveImage {
  width: 100%;
}

.page404 .header .left img {
  width: 100%;
}

.Home .header .left {
  padding: 10em 4em;
  min-height: 80vh;
}

.Home .header .left #HDLR {
  font-size: 120px;
  position: absolute;
}

.Home .header .left h1 {
  font-size: 27px;
}

.Home .header .left h4 {
  font-size: 18px;
  font-weight: 300;
}

.Home .header .right img {
  width: 100%;
}

.Home .botton {
  background: #171717;
  padding: 2em 1em 0em 1em;
  text-align: center;
}

.Home .botton h1 {
  font-size: 50px;
  color: #fff;
}
